</template>
<script src="https://code.jquery.com/jquery-3.6.0.min.js"></script>  
  <script>
export default {
  data() {
    return {
      filters: [
        { text: "显示全部", value: "all" },
        { text: "重大风险", value: "banana" },
        { text: "一般风险", value: "apple" }
      ],
      fruits: [
        { id: 1, name: "> 交易主体不适格及信息缺失" },
        { id: 2, name: "> 未标注交易标的" },
        { id: 3, name: "> 税费需双方共同承担 "},
        { id: 4, name: " > 一方责任过重" },
      ],
      currentFilter: "all", // 初始筛选条件
      activeFruit: 0
    };
  },
  computed: {
    filteredFruits() {
      if (this.currentFilter === "all") {
        // 如果筛选条件是 "all"，则返回所有水果
        return this.fruits;
      } else {
        // 否则，返回与筛选条件完全匹配的水果
        return this.fruits.filter(fruit => fruit.name === this.currentFilter);
      }
    }
  },
  methods: {
    applyFilter(filterValue) {
      this.currentFilter = filterValue;
    },
    setActiveFruit(fruitId) {
      this.activeFruit = fruitId; // 更新被点击的水果
    },
    handleFileChange(e) {
    const file = e.target.files[0];
    this.uploadFile(file);
  },
  uploadFile(file) {
    let formData = new FormData();
    formData.append("file", file);

    // 使用axios上传文件
    axios
      .post("http://localhost:3000/upload", formData, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
      .then(response => {
        console.log(response);
        // 处理上传后的操作
      })
      .catch(error => console.error(error));
  }
  },
 
};
</script>
  <style scoped>
