}
.fruit-list {
  /* 水果列表的样式 */
  list-style-type: none;
  padding: 0;
  margin: 30px;
  border: solid 1px gainsboro;
  width: 80%; /* 根据需要设置列表宽度 */
}

.fruit-list li {
